import { Component, OnInit } from '@angular/core';

import { SidebarService } from 'src/app/services/sidebar/sidebar.service';
import { Incident } from 'src/app/services/incidents/incident';
import { ActivatedRoute } from '@angular/router';
import { ImagesService } from 'src/app/services/images/images.service';
import { IncidentsService } from 'src/app/services/incidents/incidents.service';

@Component({
  selector: 'app-incident-card-page',
  templateUrl: './incident-card-page.component.html',
  styleUrls: ['./incident-card-page.component.scss']
})
export class IncidentCardPageComponent implements OnInit {

  private webId: string = undefined;
  public incident: Incident = null;

  constructor(
    private sidebarService: SidebarService,
    private route: ActivatedRoute,
    private imagesService: ImagesService,
    private incidentsService: IncidentsService

  ) {
    this.sidebarService.handleSetSettings({
      showSidebar: false,
      bgColor: '#fff'
    });
  }

  public prepareLink(link: string): string {
    return '"' + this.imagesService.prepareBackgroundUrl(link) + '"';
  }

  ngOnInit() {
    this.webId =
      this.route.snapshot.data.webId !== 'root'
        ? this.route.snapshot.data.webId
        : null;

    this.incidentsService.getItem({id: this.route.snapshot.params.itemId, webId: this.webId})
      .subscribe(item => this.incident = item);
  }
}
