import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { IncidentCardPageComponent } from './components/incident-card-page/incident-card-page.component';
import { IncidentsPageComponent } from './components/incidents-page/incidents-page.component';
import { GroupsResolver } from 'src/app/services/security/groups.resolver';
import { IncidentsResolver } from 'src/app/services/incidents/incidents.resolver';
import { IncidentCardResolver } from 'src/app/services/incidents/incident-card.resolver';
import { CompanyWebResolver } from '../services/companies/company-web.resolver';

const routes: Routes = [
  {
    path: 'root',
    component: IncidentsPageComponent,
    resolve: {
      groups: GroupsResolver,
      webId: CompanyWebResolver
    },
    data: { animation: 'fadeAnimation', title: 'Происшествия' }
  },
  {
    path: 'root/:itemId',
    component: IncidentCardPageComponent,
    resolve: {
      groups: GroupsResolver,
      webId: CompanyWebResolver,
    },
    data: { animation: 'fadeAnimation', title: 'Происшествия' }
  },
  {
    path: ':id',
    component: IncidentsPageComponent,
    resolve: {
      groups: GroupsResolver,
      webId: CompanyWebResolver
    },
    data: { animation: 'fadeAnimation', title: 'Происшествия' }
  },
  {
    path: ':id/:itemId',
    component: IncidentCardPageComponent,
    resolve: {
      groups: GroupsResolver,
      webId: CompanyWebResolver,
    },
    data: { animation: 'fadeAnimation', title: 'Происшествия' }
  },
  {
    path: '',
    redirectTo: 'root',
    pathMatch: 'full'
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  providers: [
    GroupsResolver,
    IncidentsResolver,
    IncidentCardResolver
  ],
  exports: [RouterModule]
})
export class IncidentsRoutingModule {}
