import { Injectable } from '@angular/core';
import { ListItemsService } from '../list-items-service';
import { Incident } from './incident';
import { SharepointService } from '../sharepoint/sharepoint.service';
import * as moment from 'moment';
import { Observable } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class IncidentsService extends ListItemsService<Incident> {

  constructor(private sharepointService: SharepointService) {
    super();
    moment.locale('ru');
    moment.updateLocale('ru', {
      calendar: {
        sameDay: '[Сегодня]',
        nextDay: 'DD MMMM YYYY',
        nextWeek: 'DD MMMM YYYY',
        lastDay: '[Вчера]',
        lastWeek: 'DD MMMM YYYY',
        sameElse: 'DD MMMM YYYY'
      }
    });
  }

  getListTitle(): string {
    return 'Происшествия';
  }

  getListRelativePath(): string {
    return '/Lists/slIncidents';
  }

  getSelect(): string {
    return 'Created,Id,Title,slText,OData__ModerationStatus,AttachmentFiles/ServerRelativeUrl';
  }

  getExpand(): string {
    return 'AttachmentFiles';
  }

  getOrderBy(): [string, boolean][] {
    return [['Created', false]];
  }

  getTop(): number {
    return 5000;
  }

  getCachingOptions(): import('@pnp/odata').ICachingOptions {
    return null;
  }

  convertListItemToEntity(item: any): Incident {
    const entity: Incident = {
      id: item.Id,
      title: item.Title,
      date: new Date(item.Created),
      dateTitle: moment(item.Created).calendar(),
      text: item.slText
    };

    if (item.AttachmentFiles) {
      entity.attachmentFiles = item.AttachmentFiles.map(a => a.ServerRelativeUrl);
    }

    return entity;
  }

  convertEntityToListItem(entity: Incident) {
    throw new Error('Method not implemented.');
  }

  getMockItems(): Incident[] {
    return [];
  }

  isMock(): boolean {
    return false;
  }
}
