import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';

import { SidebarService } from 'src/app/services/sidebar/sidebar.service';
import { Incident } from 'src/app/services/incidents/incident';
import * as moment from 'moment';
import { IncidentsService } from 'src/app/services/incidents/incidents.service';

export interface WithoutIncidents {
  daysCount: number;
  daysHumanize: string;
}

@Component({
  selector: 'app-incidents-page',
  templateUrl: './incidents-page.component.html',
  styleUrls: ['./incidents-page.component.scss']
})

export class IncidentsPageComponent implements OnInit {
  private webId: string = undefined;
  public withoutIncidents: WithoutIncidents = null;
  public incidentsList: Incident[] = [];

  constructor(
    private sidebarService: SidebarService,
    private route: ActivatedRoute,
    private incidentsService: IncidentsService
  ) {
    this.sidebarService.handleSetSettings({
      showSidebar: false,
      bgColor: '#fff'
    });
  }

  getIncidents() {
    this.incidentsService.getItems({filter: 'OData__ModerationStatus eq 0',
      webId: this.webId}).subscribe(items => {
        this.incidentsList = items;
        if (this.incidentsList.length > 0) {
          const incidentDate = moment(this.incidentsList[0].date, 'YYYY-MM-DD');
          const currentDate = moment().startOf('day');
          const countDays = moment.duration(incidentDate.diff(currentDate)).as('days');
          this.withoutIncidents = {
            daysCount: Math.abs(countDays),
            daysHumanize: moment.duration(countDays, 'days').humanize()
          };
        }
      });
  }

  ngOnInit() {
    this.webId =
      this.route.snapshot.data.webId !== 'root'
        ? this.route.snapshot.data.webId
        : null;
    this.getIncidents();
  }
}
