import { Injectable } from '@angular/core';
import { Resolve, ActivatedRouteSnapshot, RouterStateSnapshot } from '@angular/router';
import { Observable } from 'rxjs';

import { Incident } from './incident';
import { IncidentsService } from './incidents.service';

@Injectable()
export class IncidentsResolver implements Resolve<Incident[]> {

  constructor(private incidentsService: IncidentsService) {}

  resolve(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): Observable<Incident[]> {
    return this.incidentsService.getItems({filter: 'OData__ModerationStatus eq 0'});
  }
}
